import React, { useState } from "react";
import AppLoader from "../loader";
import "./index.css";

const ProjectItem = ({ item, onChange }) => {
  const [showLoader, setShowloader] = useState(true);
  return (
    <div className="project-item" key={item.id}>
      <div className="project-item-box">
        <img
          className="shadow-sm"
          src={item.pro_image}
          alt={item.title}
          onLoad={() => setShowloader(false)}
        />
        <div className="projectDetail">
          {item.url && (
            <a
              className="text-white"
              href={item.url}
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="material-icons cursor">link</i> &nbsp;
            </a>
          )}{" "}
          <i className="material-icons cursor" onClick={() => onChange(item)}>
            fullscreen
          </i>
        </div>
        {showLoader && <AppLoader />}
      </div>
    </div>
  );
};

export default ProjectItem;
